strong{font-weight:500;}
p{letter-spacing:-0.03em}
body{
	font-family:$fontSet;
	font-family:'Noto Sans';
	font-weight:300;
	color:$color_default;
	font-size:$defaultfontsize;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	overflow-x:hidden;
	line-height:1.7;
}
.keyboardControlActive{
	transform:translate3d(0, 0, 0) !important;
}
// 관련사이트
.aside_site{
	margin:30px 0 15px;
	padding:20px;
	background-color:#f0f2f7;
	>h2{
		font-size:16px;
		padding-left:24px;
		background: url(/static/park/img/common/ico-relationSite.png) 0 center no-repeat;
		background-size:14px;
		margin-bottom:5px;
	}
	ul{
		overflow:hidden;
		margin:0 -5px;
		li{
			margin:2px 0;
			@include breakpoint($lg){
				margin:5px 0;
				float:left;
				padding:0 5px;
				width:33.33%;
			}
			a{
				display:block;
				background-color:#fff;
				border:1px solid #dee0e6;
				padding:9px 0;
				text-align:center;
			}
		}
	}
}
.iv{
	@include breakpoint($sm){
		// overflow-x:auto;
		// div{min-width:620px;}
		position:relative;
		img{vertical-align:top;}
		&:after{
			content: '';
			font-family:'LineAwesome';
			display:block;
			position:absolute;
			right:10px;
			top:10px;
			// bottom:0;
			width:60px;
			height:60px;
			text-align:center;
			background-color:rgba(0,0,0, 0.75);
			color:#fff;
			padding-top:7px;
			font-size:30px;
			line-height:1;
			vertical-align:top;
			border-radius:25px;
		}
		&:before{
			content: '원본';
			text-align:center;
			position:absolute;
			// right:0;
			// bottom:9px;
			right:10px;
			top:46px;
			width:60px;
			text-align:center;
			z-index:2;
			color:#fff;
		}
	}
	&.line{
		border:1px solid $bdcolor;
		border-radius:3px;
		text-align:center;
		padding:20px;
		margin-top:20px;
		@include breakpoint($md){
			padding-top:30px;padding-bottom:30px;
		}
		@include breakpoint($lg){
			padding-top:40px;padding-bottom:40px;
		}
	}
}
//
.iframe{
	width:100%;
	height:500px;
}
.dllist{
	dl{
		display:table-row;
		>*{
			display:table-cell;
			padding-bottom:10px;
		}
	}
	dt{
		white-space:nowrap;
		color:#424f88;
		&:before {
			content: '';
			width:4px;
			height:4px;
			display:inline-block;
			overflow:hidden;
			border-radius:50%;
			background-color:#b8b8b8;
			vertical-align:middle;
			margin-right:6px;
		}
		position:relative;
		padding-right:8px;
		&:after {
			content: ':';
			position:absolute;
			right:0;
			top:0;
		}
	}
	dd{
		padding-left:10px;
	}
	.bu2+&{margin-left:20px;}
}
